y=100 finishopacity=50 style=1 startx=0, starty=0, finishx=0, finishy=150) progid: DXImageTransform.Microsoft.gradient(startcolorstr=$color1, endcolorstr=$color2, gradientType=1);\r\n    /*IE8*/\r\n    background: $color2;\r\n    /*  */\r\n    background: -webkit-linear-gradient($color1, $color2);\r\n    background: -o-linear-gradient( $color1, $color2);\r\n    background: -moz-linear-gradient( $color1, $color2);\r\n    background: linear-gradient( $color1, $color2);\r\n}\r\n\r\n//\r\n@mixin gradientLeft($color1, $color2) {\r\n    // filter: alpha(opacity=100 finishopacity=50 style=1 startx=0, starty=0, finishx=0, finishy=150) progid: DXImageTransform.Microsoft.gradient(startcolorstr=$color1, endcolorstr=$color2, gradientType=0);\r\n    // -ms-filter: alpha(opacity=100 finishopacity=50 style=1 startx=0, starty=0, finishx=0, finishy=150) progid: DXImageTransform.Microsoft.gradient(startcolorstr=$color1, endcolorstr=$color2, gradientType=0);\r\n    /*IE8*/\r\n    background: $color2;\r\n    /*  */\r\n    background: -webkit-linear-gradient(left, $color1, $color2);\r\n    background: -o-linear-gradient(left, $color1, $color2);\r\n    background: -moz-linear-gradient(left, $color1, $color2);\r\n    background: linear-gradient(left, $color1, $color2);\r\n}\r\n\r\n//transition\r\n@mixin transition($val) {\r\n    -webkit-transition: all $val;\r\n    -moz-transition: all $val;\r\n    -ms-transition: all $val;\r\n    -o-transition: all $val;\r\n    transition: all $val;\r\n}\r\n\r\n//\r\n@mixin noSelect() {\r\n    -webkit-user-select: none;\r\n    -moz-user-select: none;\r\n    -ms-user-select: none;\r\n    user-select: none;\r\n}\r\n\r\n// \r\n@mixin moreRowOverflow($num) {\r\n    overflow: hidden;\r\n    text-overflow: ellipsis;\r\n    display: -webkit-box;\r\n    -webkit-line-clamp: $num;\r\n    -webkit-box-orient: vertical;\r\n}\r\n\r\n@mixin borderBoxSet {\r\n    box-sizing: border-box;\r\n    -moz-box-sizing: border-box;\r\n    -webkit-box-sizing: border-box;\r\n}\r\n\