//         if (str && typeof str == 'string') {
//             oriLogFunc.call(console, "%c%s", "color:green", str);
//         } else {
//             oriLogFunc.call(console, str);
//         }

//     }
// })(console.log);
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>'
})


